import { CompanyService } from './../../services/company.service';
import { Observable } from 'rxjs';
import { LoadingController, ToastController } from '@ionic/angular';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AuthService } from 'src/app/services/auth.service';
import { Register } from 'src/app/models/register.model';
import { take     } from 'rxjs/operators';

@Component({
  selector: 'app-register',
  templateUrl: './register.page.html',
  styleUrls: ['./register.page.scss'],
})
export class RegisterPage implements OnInit {
  form: FormGroup
  companies!:any;
  constructor(
    private router:     Router,
    private authSrv:    AuthService,
    private loadCtrl:   LoadingController,
    private toastCtrl:  ToastController,
    private companySrv: CompanyService 
  ) { }

  ngOnInit() {
  
    this.initialFormRegister()
    console.log(this.loadCompanys())
  }

  initialFormRegister(){
    this.form = new FormGroup({
      name:                  new FormControl(null,[Validators.required]),
      email:                 new FormControl(null,[Validators.required]), 
      company_id:            new FormControl(null,[Validators.required]), 
      gender:                new FormControl(null,[Validators.required]), 
      birth:                 new FormControl(null,[Validators.required]), 
      type:                  new FormControl(null,[Validators.required]), 
      password:              new FormControl(null,[Validators.required]),
      password_confirmation: new FormControl(null,[Validators.required]),   
    })
  }

  onLogin(){
    this.router.navigate(['login']);
  }

  loadCompanys(){
    this.companySrv.getAllCompany().subscribe(data => {
      this.companies = data;
    })
  }

  onCompleteProfile(){
    this.router.navigate(['complete-profile']);
  }

  onClick(){

  }

  async submitRegisterUser(){
    const loading = await this.loadCtrl.create({message:"Enviando os Dados..."});
    loading.present();
    const toastSuccess = await this.toastCtrl.create({
      message:'Usuário Criado com Sucesso!',
      duration: 3000
    });
    const toastError = await this.toastCtrl.create({
      message:'Falha ao Criar Usuário',
      duration: 3000
    }); 
    let request: Observable<Register>;
    let dados     = this.form.value
    dados['type'] = 3
    request = this.authSrv.register(dados);
    request.pipe(take(1)).subscribe(()=>{
      this.router.navigate(['login'])
      loading.dismiss();
      toastSuccess.present();
    },(err)=>{
       loading.dismiss();
       toastError.present();
       console.error(err); 
    })
  }

}
